/* Colors */
:root{
    --yellow: #FBB248;
    --green: #475C23;
    --beige: #E3DCC9;
    --grey: #ECECEC;
}
/* Reset */
* {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
    border: none;
    text-decoration: none;
  }


/* General */
.btn{
    width: 300px;
    height: 50px;
    border-radius: 20px;
    font-size: 14pt;
    background-color: #FBB248;
    background-color: var(--yellow);
    font-weight: 600;
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    color: white;
    margin-top: 25px;
}
.btn:hover{opacity: 80%;}
.btn:active{opacity: 60%;}



.btn-green{
    background-color: #475C23;
    background-color: var(--green);
}
.btn-green:hover{opacity: 90%;}
.btn-green:active{opacity: 70%;}

.btn-add{
    width: 40px;
    height: 40px;
    background-color: #475C23;
    background-color: var(--green);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-decoration: none;
    font-size: 40px;
    border-radius: 45px;
    
}

.btn-group{
    background-color: #E3DCC9;
    background-color: var(--beige);
    color: rgb(49, 49, 49);
}

.add-container{
    width: 90vw;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    text-decoration: none;
}



.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.row{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}


.log-out{
    display: flex;
    flex-direction: column;
    margin: 10px;
    line-height: 1.5;
    text-align: center;
}

.log-out h4{
    font-size: 20px;
    display: flex;
    position: center;
    border: 2px solid black;
    border-radius: 10px;
    background-color: #ECECEC;
    background-color: var(--grey);
    padding-top: 5px;
    padding-bottom: 5px;
}


.text-input{
    width: 310px;
    height: 50px;
    margin-top: 25px;
    font-family: 'Montserrat', sans-serif;
    border: none;
    border-radius: 20px;
    background-color: #ffffff;
    text-align: center;
    font-size: 14pt;
}
.text-input-grey{
    width: 310px;
    height: 50px;
    margin-top: 25px;
    font-family: 'Montserrat', sans-serif;
    border: none;
    border-radius: 20px;
    background-color: #ECECEC;
    background-color: var(--grey);
    text-align: center;
    font-size: 14pt;
}

.paragraph-link{
    font-size: inherit;
    color: black;
    text-decoration: none;
    font-weight: bolder;  
}


/* Tasks */

.task-card {
    display: grid;
    grid-template-columns: 0.3fr 2.4fr 0.3fr;
    grid-template-rows: 1fr;
    grid-auto-rows: 1fr;
    grid-auto-flow: row;
    justify-content: center;
    align-items: center;
    grid-template-areas:
      "task-card-img task-card-name task-card-done";
    background-color: #ECECEC;
    background-color: var(--grey);
    border-radius: 15px;
    margin: 5px;
    width: 310px;
    padding: 5px;
  }
  
  .task-card-img { grid-area: task-card-img;
    width: 50px;
    overflow: hidden;
}
  
  .task-card-name { grid-area: task-card-name;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    align-items: center;
}
.task-card-name h3 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 20px;
}
.task-card-name p {
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    font-size: 15px;
}
  
  .task-card-done { grid-area: task-card-done; }
  .task-card-done button { 
      width: 40px; 
      height: 40px; 
      border-radius: 45px;
      background-color: red; }
  

/* Get Started */
.get-started{
    text-align: center; 
}
.get-started h1{
    padding-bottom: 20px;
    padding-top: 50px;
    font-size: 40pt;
    max-width: 90vw;
}
.get-started span{
    color: #FBB248;
    color: var(--yellow);
}
.get-started p{
    font-size: 15pt;
    max-width: 90vw;
    color: rgb(75, 75, 75);
}
.image-home{
    background-image: url(/app/static/media/pplHome.868facec.png);
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: cover; 
    width: 350px;
    height: 370px;
}

/* Login Page */

.login-container{
    width: 100vw;
    position: fixed;
    bottom: 0;
    background-color: #ECECEC;
    background-color: var(--grey);
    padding-top: 40px;
    padding-bottom: 50px;
    border-radius: 25px 25px 0 0 ;
    box-shadow: 4px -3px 6px #c3c3c3;
}
.login-container h2{
    text-align: center;
    font-size: 20pt;
}
.login-container p{
    text-align: center;
    font-size: 15pt;
    color: rgb(75, 75, 75);
}
.login-container span{
    text-align: center;
    font-size: 11pt;
    color: rgb(75, 75, 75);
}q

.profile-avatar{
    margin-top: 15px;
    width: 120px;
}


/* Groups */
.group-card{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ECECEC;
    background-color: var(--grey);
    border-radius: 15px;
    margin: 5px;
    width: 310px;
    padding: 5px;
}
.group-card h3 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 20px;
}
.group-heading{
    font-family: 'Lato', sans-serif; 
    font-size: 18pt;
    font-weight: 500;
}





