.react-calendar {
  width: 350px;
  max-width: 100%;
  background: var(--beige);
  border-radius: 0 0 25px 25px ;
  box-shadow: 0px 5px 10px #c3c3c3;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.125em;
}
.react-calendar--doubleView {
  width: 700px;

}
.react-calendar--doubleView .react-calendar__viewContainer {
  display: flex;
  margin: -0.5em;

}
.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  box-sizing: border-box;
  border-radius: 0 0 25px 25px;
}
.react-calendar button {
  margin: 0;
  border: none;
/*  border: 1px solid gray;
  border-radius: 5px; */
  outline: none;
  background: var(--beige);
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}
.react-calendar__navigation {
  display: flex;
  height: 44px;
  margin-bottom: 1em;
}
.react-calendar__navigation button {
  min-width: 44px;
  background: none;
  border: none;
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #eee9dc;
}
.react-calendar__navigation button[disabled] {
  background-color: #f0f0f0;
}



.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75em;

}
.react-calendar__month-view__weekdays__weekday {
  padding: 0.5em;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75em;
  font-weight: bold;
  padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
}
.react-calendar__month-view__days__day--weekend {
  color: #6f9700;
}
.react-calendar__month-view__days__day--neighboringMonth {
  color: #757575;
}
.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
}
.react-calendar__tile {
  max-width: 100%;
  text-align: center;
  padding: 0.75em 0.5em;
  background: #fdfdfd;
  
}
.react-calendar__tile:disabled {
  background-color: #f0f0f0;
}
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #eee9dc;
}
.react-calendar__tile--now {
  background: #ffff76;
}
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
  background: #ffffa9;
}
.react-calendar__tile--hasActive {
  background: #76baff;
}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
  background: #a9d4ff;
}
.react-calendar__tile--active {
  background: #006edc;
  color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #1087ff;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #e6e6e6;
}

:root{
  --yellow: #FBB248;
  --green: #475C23;
  --beige: #E3DCC9;
  --grey: #ECECEC;
}


.avatar-grid {
    height: 100%;
    margin: 0;
    display: grid;
    width: 290px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 15px 15px;
    gap: 15px 15px;
    grid-auto-flow: row;
    justify-content: center;
    align-items: center;
    justify-items: center;
    grid-template-areas:
      "avatar1 avatar2 avatar3"
      "avatar4 avatar5 avatar6"
      "avatar7 avatar8 avatar9"
      "avatar10 avatar11 avatar12"
      "avatar13 avatar14 avatar15";
  }

  
  /* Selection styling */
  
  .avatar-grid * {
    border-radius: 25px;
    position: relative;
    background-color: #ECECEC;
    background-color: var(--grey); 
    background-position: center; 
    background-repeat: no-repeat; 
    background-size: cover;
    width: 70px;
    height: 70px;
  }
  
  .avatar-grid *:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    align-items: center;
    justify-content: center;
  }

  .avatar-grid *:active {
    background-color: #FBB248;
    background-color: var(--yellow);
  }

  .avatar-grid * label,[type=radio] { 
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* IMAGE STYLES */
  [type=radio] {
    position: absolute;
    opacity: 0;
  }
  
  [type=radio]+div {
    cursor: pointer;
    margin-right: 0.5rem;
  }
  
  [type=radio]:checked + div {
    outline: 4px solid #FBB248;
    outline: 4px solid var(--yellow);
    background-color: #FBB248;
    background-color: var(--yellow);
    opacity: 70%;
  }

  .profile-text-input{
    width: 80vw;
    height: 50px;
    margin-top: 25px;
    font-family: 'Montserrat', sans-serif;
    border: none;
    border-radius: 20px;
    background-color: #ECECEC;
    background-color: var(--grey);
    text-align: center;
    font-size: 16pt;
}



